import React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { jsPDF } from 'jspdf';

const BookingConfirmation = () => {
    const location = useLocation();
    const navigate = useNavigate();
    const { booking, show, seats, paymentMethod } = location.state || {};

    if (!booking) {
        return (
            <div className="confirmation-page">
                <div className="confirmation-card">
                    <h1 style={{ color: 'var(--text-primary)' }}>No Booking Data</h1>
                    <p>It seems you navigated here without a booking.</p>
                    <button className="btn btn-primary" onClick={() => navigate('/')}>Go to Home</button>
                </div>
            </div>
        );
    }

    const formatDate = (d) => d ? new Date(d).toLocaleDateString('en-IN', { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' }) : 'N/A';

    const movieTitle = booking.movie?.title || show?.movie?.title || 'Movie';
    const posterUrl = booking.movie?.posterUrl || show?.movie?.posterUrl;
    const seatList = (seats || booking.seats || []).sort().join(', ');
    const showDate = formatDate(show?.date || booking.show?.date);
    const showTime = show?.startTime || booking.show?.startTime || 'N/A';
    const screen = show?.screen || booking.show?.screen;
    const paymentLabel = { upi: 'UPI', card: 'Card', cash: 'Cash at Counter' }[paymentMethod] || paymentMethod || 'N/A';

    const downloadTicket = () => {
        const doc = new jsPDF();

        doc.setFillColor(11, 12, 16);
        doc.rect(0, 0, 210, 297, 'F');

        doc.setFillColor(229, 9, 20);
        doc.rect(0, 0, 210, 40, 'F');
        doc.setTextColor(255, 255, 255);
        doc.setFontSize(22);
        doc.setFont('helvetica', 'bold');
        doc.text('MovieBook', 105, 18, { align: 'center' });
        doc.setFontSize(12);
        doc.setFont('helvetica', 'normal');
        doc.text('Booking Confirmation', 105, 30, { align: 'center' });

        doc.setTextColor(245, 245, 245);
        doc.setFontSize(18);
        doc.setFont('helvetica', 'bold');
        doc.text(movieTitle, 105, 60, { align: 'center' });

        doc.setDrawColor(229, 9, 20);
        doc.setLineWidth(0.5);
        doc.line(30, 68, 180, 68);

        const details = [
            ['Booking ID', booking.bookingId || booking._id],
            ['Seats', seatList],
            ['Date', showDate],
            ['Show Time', showTime],
            ['Screen', screen || 'N/A'],
            ['Payment', paymentLabel],
            ['Total Amount', `Rs. ${booking.totalPrice}`],
        ];

        let y = 82;
        doc.setFontSize(11);
        details.forEach(([label, value]) => {
            doc.setTextColor(160, 160, 176);
            doc.setFont('helvetica', 'normal');
            doc.text(label, 35, y);
            doc.setTextColor(245, 245, 245);
            doc.setFont('helvetica', 'bold');
            doc.text(String(value), 175, y, { align: 'right' });
            y += 14;
        });

        doc.setDrawColor(229, 9, 20);
        doc.line(30, y + 2, 180, y + 2);

        doc.setTextColor(0, 200, 83);
        doc.setFontSize(14);
        doc.text('Enjoy your movie!', 105, y + 18, { align: 'center' });

        doc.setTextColor(107, 107, 128);
        doc.setFontSize(8);
        doc.setFont('helvetica', 'normal');
        doc.text('This is your electronic ticket. Please show this at the cinema entrance.', 105, y + 30, { align: 'center' });

        doc.save(`MovieBook_Ticket_${booking.bookingId || booking._id}.pdf`);
    };

    return (
        <div className="confirmation-page">
            <div className="confirmation-card">
                <div className="confirmation-icon"></div>
                <h1>Booking Confirmed!</h1>
                <p>Your tickets have been booked successfully</p>

                {posterUrl && <img src={posterUrl} alt={movieTitle} className="confirmation-poster" />}

                <div className="confirmation-details">
                    <div className="confirmation-detail-row">
                        <span className="label">Booking ID</span>
                        <span className="value">{booking.bookingId || booking._id}</span>
                    </div>
                    <div className="confirmation-detail-row">
                        <span className="label">Movie</span>
                        <span className="value">{movieTitle}</span>
                    </div>
                    <div className="confirmation-detail-row">
                        <span className="label">Seats</span>
                        <span className="value">{seatList}</span>
                    </div>
                    <div className="confirmation-detail-row">
                        <span className="label">Date</span>
                        <span className="value">{showDate}</span>
                    </div>
                    <div className="confirmation-detail-row">
                        <span className="label">Show Time</span>
                        <span className="value">{showTime}</span>
                    </div>
                    {screen && (
                        <div className="confirmation-detail-row">
                            <span className="label">Screen</span>
                            <span className="value">{screen}</span>
                        </div>
                    )}
                    <div className="confirmation-detail-row">
                        <span className="label">Payment</span>
                        <span className="value">{paymentLabel}</span>
                    </div>
                    <div className="confirmation-detail-row">
                        <span className="label">Total Amount</span>
                        <span className="value total">{booking.totalPrice}</span>
                    </div>
                </div>

                <div className="confirmation-actions">
                    <button className="btn btn-success" onClick={downloadTicket}> Download Ticket</button>
                    <button className="btn btn-primary" onClick={() => navigate('/')}>Back to Home</button>
                </div>
            </div>
        </div>
    );
};

export default BookingConfirmation;
