import React, { useState, useEffect, useCallback } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { getMovieById, getShows } from '../services/api';
import { useAuth } from '../context/AuthContext';
import Loader from '../components/Loader';

const getYouTubeId = (url) => {
  if (!url) return null;
  const match = url.match(/(?:youtube\.com\/(?:watch\?v=|embed\/)|youtu\.be\/)([^&?\s]+)/);
  return match ? match[1] : null;
};

const MovieDetails = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const { user } = useAuth();
  const [movie, setMovie] = useState(null);
  const [shows, setShows] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [liked, setLiked] = useState(false);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const [movieRes, showsRes] = await Promise.all([getMovieById(id), getShows(id)]);
        setMovie(movieRes.data);
        setShows(showsRes.data);
      } catch {
        setError('Failed to load movie details.');
      } finally {
        setLoading(false);
      }
    };
    fetchData();
  }, [id]);

  useEffect(() => {
    try {
      const likedMovies = JSON.parse(localStorage.getItem('likedMovies') || '[]');
      setLiked(likedMovies.includes(id));
    } catch { setLiked(false); }
  }, [id]);

  const toggleLike = useCallback(() => {
    try {
      let likedMovies = JSON.parse(localStorage.getItem('likedMovies') || '[]');
      if (likedMovies.includes(id)) {
        likedMovies = likedMovies.filter((m) => m !== id);
        setLiked(false);
      } else {
        likedMovies.push(id);
        setLiked(true);
      }
      localStorage.setItem('likedMovies', JSON.stringify(likedMovies));
    } catch { }
  }, [id]);

  const handleBookNow = (showId) => {
    if (!user) { navigate('/login'); return; }
    navigate(`/booking/${showId}/${id}`);
  };

  const formatDate = (d) => d ? new Date(d).toLocaleDateString('en-IN', { weekday: 'short', year: 'numeric', month: 'short', day: 'numeric' }) : 'N/A';
  const youtubeId = movie ? getYouTubeId(movie.trailerUrl) : null;

  if (loading) return <Loader text="Loading movie details..." />;
  if (error) return <div className="page-container"><div className="error-msg">{error}</div></div>;
  if (!movie) return null;

  return (
    <div className="page-container">
      <div className="movie-detail-banner">
        {movie.posterUrl && <div className="movie-detail-banner-bg" style={{ backgroundImage: `url(${movie.posterUrl})` }}></div>}
        <div className="movie-detail-banner-gradient"></div>
        <div className="movie-detail-content">
          <div>
            {movie.posterUrl ? (
              <img src={movie.posterUrl} alt={movie.title} className="movie-detail-poster" />
            ) : (
              <div className="movie-detail-poster-placeholder"></div>
            )}
          </div>
          <div className="movie-detail-info">
            <h1>{movie.title}</h1>
            <div className="movie-detail-badges">
              {movie.language && <span className="detail-badge lang-b">{movie.language}</span>}
              {movie.genre && movie.genre.map((g, i) => <span key={i} className="detail-badge genre-b">{g}</span>)}
              {movie.duration && <span className="detail-badge dur-b">{movie.duration} min</span>}
              {movie.rating > 0 && <span className="detail-badge rating-b"> {movie.rating.toFixed(1)}</span>}
            </div>
            {movie.releaseDate && <p style={{ color: 'var(--text-muted)', fontSize: '0.88rem', marginBottom: '1rem' }}>Release: {formatDate(movie.releaseDate)}</p>}
            {movie.description && <p className="movie-detail-desc">{movie.description}</p>}
            <div className="movie-detail-actions">
              <button className={`like-btn ${liked ? 'liked' : ''}`} onClick={toggleLike} title={liked ? 'Unlike' : 'Like'}>
                {liked ? '' : ''}
              </button>
            </div>
          </div>
        </div>
      </div>

      {youtubeId && (
        <div className="trailer-section">
          <h2> Trailer</h2>
          <div className="trailer-wrapper">
            <iframe
              src={`https://www.youtube.com/embed/${youtubeId}?rel=0`}
              title="Movie Trailer"
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
              allowFullScreen
            />
          </div>
        </div>
      )}

      <div className="shows-section">
        <h2> Available Shows</h2>
        {shows.length === 0 ? (
          <div className="empty-state"><div className="empty-state-icon"></div><h3>No shows available</h3><p>Check back later for upcoming show times</p></div>
        ) : (
          <div className="shows-grid">
            {shows.map((show) => (
              <div key={show._id} className="show-card">
                <div className="show-card-time">{show.startTime || 'TBA'}</div>
                <div className="show-card-date">{formatDate(show.date)}</div>
                <div className="show-card-info">
                  <span className="show-card-price">{show.price || 200}</span>
                  {show.screen && <span className="show-card-screen">Screen: {show.screen}</span>}
                </div>
                <button className="btn btn-primary btn-sm" style={{ width: '100%', marginTop: '12px' }} onClick={() => handleBookNow(show._id)}>Book Now</button>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
};

export default MovieDetails;
