import React, { useState, useEffect } from 'react';
import { getMovies } from '../services/api';
import MovieCard from '../components/MovieCard';
import Loader from '../components/Loader';

const LikedMovies = () => {
    const [movies, setMovies] = useState([]);
    const [likedIds, setLikedIds] = useState([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const fetchMovies = async () => {
            try {
                const ids = JSON.parse(localStorage.getItem('likedMovies') || '[]');
                setLikedIds(ids);
                if (ids.length > 0) {
                    const res = await getMovies();
                    setMovies(res.data.filter((m) => ids.includes(m._id)));
                }
            } catch { } finally {
                setLoading(false);
            }
        };
        fetchMovies();
    }, []);

    const removeLiked = (movieId, e) => {
        e.stopPropagation();
        const updated = likedIds.filter((id) => id !== movieId);
        localStorage.setItem('likedMovies', JSON.stringify(updated));
        setLikedIds(updated);
        setMovies((prev) => prev.filter((m) => m._id !== movieId));
    };

    if (loading) return <Loader text="Loading liked movies..." />;

    return (
        <div className="page-container">
            <div className="page-header">
                <h1> Liked Movies</h1>
                <p>Movies you've saved for later</p>
            </div>

            {movies.length === 0 ? (
                <div className="empty-state">
                    <div className="empty-state-icon"></div>
                    <h3>No liked movies yet</h3>
                    <p>Like movies from the details page and they'll appear here</p>
                </div>
            ) : (
                <div className="movie-grid">
                    {movies.map((movie) => (
                        <MovieCard key={movie._id} movie={movie}>
                            <button
                                className="liked-remove-btn"
                                onClick={(e) => removeLiked(movie._id, e)}
                                title="Remove from liked"
                            >
                                
                            </button>
                        </MovieCard>
                    ))}
                </div>
            )}
        </div>
    );
};

export default LikedMovies;
