import React, { useState, useEffect } from 'react';
import { getUserBookings } from '../services/api';
import { useAuth } from '../context/AuthContext';
import Loader from '../components/Loader';

const BookingHistory = () => {
  const { user } = useAuth();
  const [bookings, setBookings] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    const fetch = async () => {
      try {
        const res = await getUserBookings(user.id);
        setBookings(res.data);
      } catch {
        setError('Failed to fetch bookings.');
      } finally {
        setLoading(false);
      }
    };
    if (user) fetch();
  }, [user]);

  const formatDate = (d) => d ? new Date(d).toLocaleDateString('en-IN', { weekday: 'short', year: 'numeric', month: 'short', day: 'numeric' }) : 'N/A';

  if (loading) return <Loader text="Loading bookings..." />;

  return (
    <div className="page-container">
      <div className="page-header">
        <h1> My Bookings</h1>
        <p>Your booking history</p>
      </div>

      {error && <div className="error-msg">{error}</div>}

      {bookings.length === 0 ? (
        <div className="empty-state">
          <div className="empty-state-icon"></div>
          <h3>No bookings yet</h3>
          <p>Book a movie and your history will appear here</p>
        </div>
      ) : (
        <div className="history-grid">
          {bookings.map((b) => (
            <div key={b._id} className="history-card">
              {b.movie?.posterUrl ? (
                <img src={b.movie.posterUrl} alt={b.movie.title} className="history-card-poster" />
              ) : (
                <div className="history-card-poster-placeholder"></div>
              )}
              <div className="history-card-body">
                <div className="history-card-header">
                  <span className="history-card-title">{b.movie?.title || 'Movie'}</span>
                  <span className={`history-card-status ${b.status || 'booked'}`}>{b.status || 'booked'}</span>
                </div>
                <div className="history-card-row">
                  <span className="label">Seats</span>
                  <span className="value">{(b.seats || []).sort().join(', ')}</span>
                </div>
                <div className="history-card-row">
                  <span className="label">Date</span>
                  <span className="value">{formatDate(b.show?.date)}</span>
                </div>
                <div className="history-card-row">
                  <span className="label">Time</span>
                  <span className="value">{b.show?.startTime || 'N/A'}</span>
                </div>
                <div className="history-card-total">
                  <span className="label">Total</span>
                  <span className="value">{b.totalPrice}</span>
                </div>
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default BookingHistory;
