import React, { useState, useEffect, useCallback } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { getShowById, getUserBookings } from '../services/api';
import { useAuth } from '../context/AuthContext';
import SeatGrid from '../components/SeatGrid';
import Loader from '../components/Loader';

const SeatSelection = () => {
    const { showId, movieId } = useParams();
    const navigate = useNavigate();
    const { user } = useAuth();
    const [show, setShow] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState('');
    const [selectedSeats, setSelectedSeats] = useState([]);
    const [bookedSeats, setBookedSeats] = useState([]);

    const fetchData = useCallback(async () => {
        try {
            const [showRes, bookingsRes] = await Promise.all([
                getShowById(showId),
                getUserBookings(user.id).catch(() => ({ data: [] })),
            ]);
            setShow(showRes.data);
            try {
                const booked = bookingsRes.data
                    .filter((b) => b.show && (b.show._id === showId || b.show === showId))
                    .flatMap((b) => b.seats || []);
                setBookedSeats(booked);
            } catch { setBookedSeats([]); }
        } catch {
            setError('Failed to load show details.');
        } finally {
            setLoading(false);
        }
    }, [showId, user.id]);

    useEffect(() => { fetchData(); }, [fetchData]);

    const toggleSeat = (seatId) => {
        setSelectedSeats((prev) =>
            prev.includes(seatId) ? prev.filter((s) => s !== seatId) : [...prev, seatId]
        );
    };

    const pricePerSeat = show?.price || 200;
    const totalPrice = selectedSeats.length * pricePerSeat;

    const handleProceedToPayment = () => {
        if (selectedSeats.length === 0) return;
        navigate('/payment', {
            state: { showId, movieId, seats: selectedSeats, totalPrice, pricePerSeat, show },
        });
    };

    if (loading) return <Loader text="Loading seats..." />;

    return (
        <div className="seat-page">
            <div className="page-header" style={{ textAlign: 'center' }}>
                <h1>Select Your Seats</h1>
                {show && (
                    <p>{show.movie?.title || 'Movie'}  {show.startTime || 'TBA'}  {show.date ? new Date(show.date).toLocaleDateString('en-IN', { weekday: 'short', month: 'short', day: 'numeric' }) : ''}</p>
                )}
            </div>

            {error && <div className="error-msg">{error}</div>}

            <SeatGrid selectedSeats={selectedSeats} bookedSeats={bookedSeats} onToggle={toggleSeat} />

            <div className="seat-summary">
                <h3>Booking Summary</h3>
                {selectedSeats.length > 0 ? (
                    <>
                        <div className="seat-summary-seats">
                            {selectedSeats.sort().map((s) => (<span key={s} className="seat-tag">{s}</span>))}
                        </div>
                        <div className="seat-total">
                            {selectedSeats.length} seat{selectedSeats.length > 1 ? 's' : ''}  {pricePerSeat} = {totalPrice}
                        </div>
                        <button className="btn btn-primary" onClick={handleProceedToPayment}>
                            Proceed to Payment
                        </button>
                    </>
                ) : (
                    <p style={{ color: 'var(--text-muted)' }}>Select seats to continue</p>
                )}
            </div>
        </div>
    );
};

export default SeatSelection;
