import React, { useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { createBooking } from '../services/api';
import Loader from '../components/Loader';

const Payment = () => {
    const location = useLocation();
    const navigate = useNavigate();
    const { showId, movieId, seats, totalPrice, pricePerSeat, show } = location.state || {};

    const [method, setMethod] = useState('upi');
    const [processing, setProcessing] = useState(false);
    const [error, setError] = useState('');

    // UPI state
    const [upiId, setUpiId] = useState('');

    // Card state
    const [cardNumber, setCardNumber] = useState('');
    const [cardExpiry, setCardExpiry] = useState('');
    const [cardCvv, setCardCvv] = useState('');
    const [cardName, setCardName] = useState('');

    if (!showId || !seats) {
        return (
            <div className="confirmation-page">
                <div className="confirmation-card">
                    <h1 style={{ color: 'var(--text-primary)' }}>No Booking Data</h1>
                    <p>Please select seats first.</p>
                    <button className="btn btn-primary" onClick={() => navigate('/')}>Go Home</button>
                </div>
            </div>
        );
    }

    const validateCard = () => {
        const num = cardNumber.replace(/\s/g, '');
        if (num.length < 13 || num.length > 19) return 'Invalid card number';
        if (!/^\d{2}\/\d{2}$/.test(cardExpiry)) return 'Expiry format: MM/YY';
        if (!/^\d{3,4}$/.test(cardCvv)) return 'Invalid CVV';
        if (!cardName.trim()) return 'Name required';
        return null;
    };

    const handlePay = async () => {
        setError('');

        if (method === 'upi' && !upiId.includes('@')) {
            setError('Enter a valid UPI ID (e.g. name@upi)');
            return;
        }

        if (method === 'card') {
            const cardErr = validateCard();
            if (cardErr) { setError(cardErr); return; }
        }

        setProcessing(true);

        // Simulate payment processing delay
        await new Promise((r) => setTimeout(r, 2000));

        try {
            const res = await createBooking({ showId, movieId, seats, paymentMethod: method });
            navigate('/booking-confirmation', {
                state: { booking: res.data.booking, show, seats, paymentMethod: method },
            });
        } catch (err) {
            setError(err.response?.data?.error || 'Payment failed. Try again.');
            setProcessing(false);
        }
    };

    const formatCardNumber = (val) => {
        const nums = val.replace(/\D/g, '').slice(0, 16);
        return nums.replace(/(.{4})/g, '$1 ').trim();
    };

    if (processing) {
        return (
            <div className="payment-page">
                <div className="payment-card">
                    <div className="payment-processing">
                        <div className="loader-spinner"></div>
                        <h2 style={{ marginTop: '1rem' }}>Processing Payment...</h2>
                        <p style={{ color: 'var(--text-muted)', marginTop: '0.5rem' }}>Please wait, do not close this page</p>
                    </div>
                </div>
            </div>
        );
    }

    return (
        <div className="payment-page">
            <div className="page-header" style={{ textAlign: 'center' }}>
                <h1> Payment</h1>
                <p>Complete your booking</p>
            </div>

            <div className="payment-card">
                <div className="payment-summary">
                    <div className="payment-summary-row">
                        <span className="label">Movie</span>
                        <span className="value">{show?.movie?.title || 'Movie'}</span>
                    </div>
                    <div className="payment-summary-row">
                        <span className="label">Seats</span>
                        <span className="value">{seats.sort().join(', ')}</span>
                    </div>
                    <div className="payment-summary-row">
                        <span className="label">Price</span>
                        <span className="value">{seats.length}  {pricePerSeat}</span>
                    </div>
                    <div className="payment-summary-row" style={{ borderTop: '1px solid var(--border)', paddingTop: '8px', marginTop: '4px' }}>
                        <span className="label" style={{ fontWeight: 600 }}>Total</span>
                        <span className="value total">{totalPrice}</span>
                    </div>
                </div>

                <div className="payment-tabs">
                    <button className={`payment-tab ${method === 'upi' ? 'active' : ''}`} onClick={() => setMethod('upi')}>
                        <span className="payment-tab-icon"></span>UPI
                    </button>
                    <button className={`payment-tab ${method === 'card' ? 'active' : ''}`} onClick={() => setMethod('card')}>
                        <span className="payment-tab-icon"></span>Card
                    </button>
                    <button className={`payment-tab ${method === 'cash' ? 'active' : ''}`} onClick={() => setMethod('cash')}>
                        <span className="payment-tab-icon"></span>Cash
                    </button>
                </div>

                {error && <div className="error-msg">{error}</div>}

                {method === 'upi' && (
                    <div className="payment-form-section">
                        <div className="form-group">
                            <label>Enter UPI ID</label>
                            <input type="text" placeholder="yourname@upi" value={upiId} onChange={(e) => setUpiId(e.target.value)} />
                        </div>
                        <div className="upi-qr">
                            <p style={{ color: 'var(--text-muted)', marginBottom: '1rem', fontSize: '0.85rem' }}>Or scan QR Code</p>
                            <div className="upi-qr-box">
                                <div style={{ textAlign: 'center' }}>
                                    <div style={{ fontSize: '3rem', marginBottom: '4px' }}></div>
                                    <div style={{ fontSize: '0.65rem', color: '#666' }}>QR Code<br />moviebook@upi<br />{totalPrice}</div>
                                </div>
                            </div>
                        </div>
                    </div>
                )}

                {method === 'card' && (
                    <div className="payment-form-section">
                        <div className="form-group">
                            <label>Card Number</label>
                            <input type="text" placeholder="1234 5678 9012 3456" value={cardNumber} onChange={(e) => setCardNumber(formatCardNumber(e.target.value))} maxLength={19} />
                        </div>
                        <div className="card-row">
                            <div className="form-group">
                                <label>Expiry</label>
                                <input type="text" placeholder="MM/YY" value={cardExpiry} onChange={(e) => setCardExpiry(e.target.value)} maxLength={5} />
                            </div>
                            <div className="form-group">
                                <label>CVV</label>
                                <input type="password" placeholder="" value={cardCvv} onChange={(e) => setCardCvv(e.target.value)} maxLength={4} />
                            </div>
                        </div>
                        <div className="form-group">
                            <label>Cardholder Name</label>
                            <input type="text" placeholder="Name on card" value={cardName} onChange={(e) => setCardName(e.target.value)} />
                        </div>
                    </div>
                )}

                {method === 'cash' && (
                    <div className="payment-form-section">
                        <div className="cash-info">
                            <div className="cash-info-icon"></div>
                            <h3>Pay at Counter</h3>
                            <p>Pay {totalPrice} at the cinema counter before the show starts. Your seats will be reserved.</p>
                        </div>
                    </div>
                )}

                <button className="btn btn-primary" style={{ width: '100%', padding: '14px' }} onClick={handlePay}>
                    {method === 'cash' ? 'Confirm Booking' : `Pay ${totalPrice}`}
                </button>
            </div>
        </div>
    );
};

export default Payment;
